import React from 'react';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import AddIcon from '@material-ui/icons/Add';
import SaveIcon from '@material-ui/icons/Save';
import DeleteIcon from '@material-ui/icons/Delete';
import { Fab } from '@material-ui/core';
import Character from './Character';

class App extends React.Component {
  constructor(props) {
    super(props);
    /**/
    if (localStorage.getItem('characters') !== null) {
      this.state = {
        characters: JSON.parse(localStorage.getItem('characters')),
        index: parseInt(localStorage.getItem('index')),
      }
    } else {
      this.state = {
        characters: [],
        index: 0,
      };
    }
       

  }

  renderCharacter(character) {
    return (
      <Accordion style={{ margin: '8px', width: '100%', maxWidth: '700px', borderRadius: '8px'}}>
        <AccordionSummary expandIcon={<ExpandMoreIcon />} style={{background: 'lightblue', borderRadius: '8px'}}>
          <p>{character.name}</p>
        </AccordionSummary>
        <AccordionDetails>
          <Character
          character={character}
          randomizeName={() => this.randomizeName(character.index)}
          randomizeBG={() => this.randomizeBackground(character.index)}
          rollSkills={() => this.rollSkills(character.index)}
          setDefaultSkills={() => this.setDefaultSkills(character.index)}
          handleNameChange={(event, i) => this.handleNameChange(event, i)}
          handleBGChange={(event, i) => this.handleBGChange(event, i)}
          handleRaceChange={(event, value, i) => this.handleRaceChange(event, value, i)}
          handleClassChange={(event, value, i) => this.handleClassChange(event, value, i)} />
        </AccordionDetails>
      </Accordion>
    )
  }

  renderCharacters() {
    return this.state.characters.map((character) =>
      <div>{this.renderCharacter(character)}</div>
    );
  }

  newCharacter() {
    this.setState({
      characters: this.state.characters.concat({
        name: this.randomName(),
        str: 15,
        dex: 14,
        con: 13,
        int: 12,
        wis: 10,
        cha: 8,
        class: "Bard",
        race: "Elf",
        background: this.randomBackground(),
        index: this.state.index,
      }),
      index: this.state.index + 1,
    });
  }

  randomName() {
    const first_names = ["Lydan" , "Syrin" , "Ptorik" , "Joz" , "Varog" , "Gethrod" , "Hezra" , "Feron" , "Ophni" , "Colborn" , "Fintis" , "Gatlin" , "Jinto" , "Hagalbar" , "Krinn" , "Lenox" , "Revvyn" , "Hodus" , "Dimian" , "Paskel" , "Kontas" , "Weston" , "Azamarr" , "Jather" , "Tekren" , "Jareth" , "Adon" , "Zaden" , "Eune" , "Graff" , "Tez" , "Jessop" , "Gunnar" , "Pike" , "Domnhar" , "Baske" , "Jerrick" , "Mavrek" , "Riordan" , "Wulfe" , "Straus" , "Tyvrik" , "Henndar" , "Favroe" , "Whit" , "Jaris" , "Renham" , "Kagran" , "Lassrin" , "Vadim" , "Arlo" , "Quintis" , "Vale" , "Caelan" , "Yorjan" , "Khron" , "Ishmael" , "Jakrin" , "Fangar" , "Roux" , "Baxar" , "Hawke" , "Gatlen" , "Barak" , "Nazim" , "Kadric" , "Paquin" , "Kent" , "Moki" , "Rankar" , "Lothe" , "Ryven" , "Clawsen" , "Pakker" , "Embre" , "Cassian" , "Verssek" , "Dagfinn" , "Ebraheim" , "Nesso" , "Eldermar" , "Rivik" , "Rourke" , "Barton" , "Hemm" , "Sarkin" , "Blaiz" , "Talon" , "Agro" , "Zagaroth" , "Turrek" , "Esdel" , "Lustros" , "Zenner" , "Baashar" , "Dagrod" , "Gentar" , "Feston" , "Syrana" , "Resha" , "Varin" , "Wren" , "Yuni" , "Talis" , "Kessa" , "Magaltie" , "Aeris" , "Desmina" , "Krynna" , "Asralyn" , "Herra" , "Pret" , "Kory" , "Afia" , "Tessel" , "Rhiannon" , "Zara" , "Jesi" , "Belen" , "Rei" , "Ciscra" , "Temy" , "Renalee" , "Estyn" , "Maarika" , "Lynorr" , "Tiv" , "Annihya" , "Semet" , "Tamrin" , "Antia" , "Reslyn" , "Basak" , "Vixra" , "Pekka" , "Xavia" , "Beatha" , "Yarri" , "Liris" , "Sonali" , "Razra" , "Soko" , "Maeve" , "Everen" , "Yelina" , "Morwena" , "Hagar" , "Palra" , "Elysa" , "Sage" , "Ketra" , "Lynx" , "Agama" , "Thesra" , "Tezani" , "Ralia" , "Esmee" , "Heron" , "Naima" , "Rydna" , "Sparrow" , "Baakshi" , "Ibera" , "Phlox" , "Dessa" , "Braithe" , "Taewen" , "Larke" , "Silene" , "Phressa" , "Esther" , "Anika" , "Rasy" , "Harper" , "Indie" , "Vita" , "Drusila" , "Minha" , "Surane" , "Lassona" , "Merula" , "Kye" , "Jonna" , "Lyla" , "Zet" , "Orett" , "Naphtalia" , "Turi" , "Rhays" , "Shike" , "Hartie" , "Beela" , "Leska" , "Vemery" , "Lunex" , "Fidess" , "Tisette" , "Partha"];
    const last_names = ["Atwater", "Agassi", "Apatow", "Akagawa", "Averescu", "Arrington", "Agrippa", "Aiken", "Albertson", "Alexander", "Amado", "Anders", "Ashsorrow", "Humblecut", "Ashbluff", "Marblemaw", "Armas", "Akka", "Aoki", "Aldrich", "Apak", "Alinsky", "Desai", "Darby", "Draper", "Dwyer", "Dixon", "Danton", "Desmith", "Ditka", "Dominguez", "Decker", "Dobermann", "Dunlop", "Dumont", "Dandridge", "Diamond", "Dobra", "Dukas", "Agnello", "Alterio", "Bidbury", "Botkin", "Benoit", "Biddercombe", "Baldwin", "Bennett", "Bourland", "Boadle", "Bender", "Best", "Bobshaw", "Bersa", "Belt", "Bourn", "Barke", "Beebe", "Banu", "Bozzelli", "Bogaerts", "Blanks", "Evert", "Eastwood", "Elway", "Eslinger", "Ellerbrock", "Eno", "Endo", "Etter", "Ebersol", "Everson", "Esapa", "Ekker", "Escobar", "Eggleston", "Ermine", "Erickson", "Keller", "Kessler", "Kobayashi", "Klecko", "Kicklighter", "Kidder", "Kershaw", "Kaminsky", "Kirby", "Keene", "Kenny", "Keogh", "Kipps", "Kendrick", "Kuang", "Fairchild", "October", "Vespertine", "Fellowes", "Omen", "Willow", "Gannon", "Presto", "Windward", "Grell", "Powers", "Wixx", "Halliwell", "Quellings", "Xanthos", "Hightower", "Quill", "Xenides", "Idlewind", "Rast", "Chamillet", "Bougaitelet", "Hallowswift", "Coldsprinter", "Winddane", "Yarrow", "Illfate", "Riddle", "Yew", "Jacaranda", "Yearwood", "Yellen", "Yaeger", "Yankovich", "Yamaguchi", "Yarborough", "Youngblood", "Yanetta", "Yadao", "Winchell", "Winters", "Walsh", "Whalen", "Watson", "Wooster", "Woodson", "Winthrop", "Wall", "Sacredpelt", "Rapidclaw", "Hazerider", "Shadegrove", "Wight", "Webb", "Woodard", "Wixx", "Wong", "Whesker", "Yale", "Yasumoto", "Yates", "Younger", "Yoakum", "York", "Rigby", "Zaba", "Surrett", "Swiatek", "Sloane", "Stapleton", "Seibert", "Stroud", "Strode", "Stockton", "Scardino", "Spacek", "Spieth", "Stitchen", "Stiner", "Soria", "Saxon", "Shields", "Stelly", "Steele", "Chanassard", "Ronchessac", "Boneflare", "Monsterbelly", "Truthbelly", "Sacredmore", "Malfoy", "Moses", "Moody", "Morozov", "Mason", "Metcalf", "McGillicutty", "Montero", "Molinari", "Marsh", "Moffett", "McCabe", "Manus", "Malenko", "Mullinax", "Morrissey", "Mantooth", "Mintz"];
    const first_name = first_names[Math.floor(Math.random() * first_names.length)];
    const last_name = last_names[Math.floor(Math.random() * last_names.length)];
    return first_name + " " + last_name;
  }

  randomElement(arr) {
    return arr[Math.floor(Math.random() * arr.length)];
  }

  randomBackground() {
    const origins = ["in a small town", "in a big city", "on a farm", "in a faraway land"];
    const motivations = ["revenge", "love", "carrying on your family's legacy", "devotion to your religion", "food", "a desire to become rich"];
    const occupations = ["soldier", "farmer", "alchemist", "blacksmith", "carpenter", "cashier", "doctor", "door to door salesperson", "chef", "con artist"];
    const origin = this.randomElement(origins);
    const motivation = this.randomElement(motivations);
    const occupation = this.randomElement(occupations);
    return "You were born " + origin + ".\nYou are motivated by " + motivation + ".\nYou currently work as a " + occupation + ".";
  }

  randomizeBackground(i) {
    let new_characters = [...this.state.characters];
    new_characters[i].background = this.randomBackground();
    this.setState({
      characters: new_characters,
    });
  }

  randomizeName(i) {
    let new_characters = [...this.state.characters];
    new_characters[i].name = this.randomName();
    this.setState({
      characters: new_characters,
    });
  }

  rollDie() {
    return Math.floor(Math.random() * 6) + 1;
  }

  skillRoll() {
    let rolls = [];
    for (let i = 0; i < 4; i++) {
      rolls.push(this.rollDie());
    }
    rolls.sort();
    return rolls[1] + rolls[2] + rolls[3];
  }

  rollSkills(i) {
    let new_characters = this.state.characters;
    new_characters[i].str = this.skillRoll();
    new_characters[i].dex = this.skillRoll();
    new_characters[i].con = this.skillRoll();
    new_characters[i].int = this.skillRoll();
    new_characters[i].wis = this.skillRoll();
    new_characters[i].cha = this.skillRoll();

    this.setState({
      characters: new_characters,
    });
  }

  setDefaultSkills(i) {
    let new_characters = this.state.characters;
    new_characters[i].str = 15;
    new_characters[i].dex = 14;
    new_characters[i].con = 13;
    new_characters[i].int = 12;
    new_characters[i].wis = 10;
    new_characters[i].cha = 8;

    this.setState({
      characters: new_characters,
    });
  }

  handleNameChange =
  (event, i) => {
      let new_characters = [...this.state.characters];
      new_characters[i].name = event.target.value;
      this.setState({
          characters: new_characters,
      });
  };

  handleBGChange =
  (event, i) => {
      let new_characters = [...this.state.characters];
      new_characters[i].background = event.target.value;
      this.setState({
          characters: new_characters,
      });
  };

  handleClassChange =
  (event, value, i) => {
      console.log(i);
      let new_characters = [...this.state.characters];
      new_characters[i].class = value;
      this.setState({
          characters: new_characters,
      });
  };

  handleRaceChange =
  (event, value, i) => {
      let new_characters = [...this.state.characters];
      new_characters[i].race = value;
      this.setState({
          characters: new_characters,
      });
  };

  save() {
    localStorage.setItem('characters', JSON.stringify(this.state.characters));
    localStorage.setItem('index', this.state.index);
  }

  delete() {
    localStorage.removeItem('characters');
    localStorage.removeItem('index');
    this.setState({
      characters: [],
      index: 0,
    });
  }

  render() {
    return (
      <div className="App">
        <div style={{display: 'flex', justifyContent: 'center', flexDirection: 'row'}}>
        <div style={{display: 'flex', justifyContent: 'center', flexDirection: 'column', flexBasis: '10%'}}>
          {this.renderCharacters()}
          </div>
        </div>
        <div style={{ position: 'absolute', right: 0, bottom: 0, margin: 8 }}>
          <Fab color="primary" aria-label="add" onClick={() => this.newCharacter()}>
            <AddIcon />
          </Fab>
          <Fab color="primary" aria-label="add" onClick={() => this.save()} style={{margin: 8 }}>
            <SaveIcon />
          </Fab>
          <Fab color="primary" aria-label="add" onClick={() => this.delete()}>
            <DeleteIcon />
          </Fab>
        </div>

      </div>
    );
  }
}

export default App;
